import {Canvas, useLoader} from "@react-three/fiber";
import { ThreeMFLoader } from "three/examples/jsm/loaders/3MFLoader";
import {OrbitControls, PerspectiveCamera} from "@react-three/drei";
import React, {Suspense} from "react";

const LoadingModel = () => {
    const model = useLoader(ThreeMFLoader, "drill/drill.3MF");
    return (
        <primitive object={model} scale={0.005} color="green"/>
        )
};

export const ThreeMFLoaderComponent = () => {
    return (
        <Canvas>
            <color attach="background" args={["lightgrey"]}/>
            <ambientLight intensity={0.5}/>
            <directionalLight position={[-1, 2, 4]} intensity={2}/>

            <Suspense fallback={null}>
                <LoadingModel/>
            </Suspense>

            <PerspectiveCamera makeDefault position={[3, 2.4, 3]} zoom={2.5} />
            <OrbitControls target={[0, 0, 0]} enableRotate={true} enableZoom={true}/>
        </Canvas>
    )
}